<template>
    <div class="filters-content" :style="{ right: isFiltersPanelVisible ? '0' : '-25vw' }">
        <button class="close-btn" @click="$emit('close-panel')">X</button>
        <p>Filters</p>
        <fieldset>
        <legend>Gender</legend>
        <div @click="logClick('Male div clicked')">
            <input type="checkbox" id="male" name="gender" value="male" v-model="selectedGender" />
            <label for="male">Male</label>
        </div>
        <div @click="logClick('Female div clicked')">
            <input type="checkbox" id="female" name="gender" value="female" v-model="selectedGender" />
            <label for="female">Female</label>
        </div>
        </fieldset>
    </div>
</template>

<script>
export default {
  props: {
    isFiltersPanelVisible: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      selectedGender: '',
    };
  },
  watch: {
    selectedGender(newGender) {
      console.log('FiltersSidePanel: selectedGender changed to', newGender);
      this.$emit('gender-filter-changed', newGender);
    },
  },
  methods: {
    logClick(message) {
      console.log(message);
    }
  }
};
</script>

