<template>
    <div>
        <div class="navbar" :class="{ dark: darkMode }">
            <div class="navbar-left">
                <!-- NAVBAR / LEFT ITEMS -->
                <div 
                    @click="changeTab('Home')" 
                    :class="{ active: activeTab = 'Home' }" 
                    class="navbar-items" 
                    v-if="this.activeTab === 'Home' && this.darkMode === false"
                >
                    <img src="../assets/home_fill.png" />
                </div>
                <div 
                    @click="changeTab('Home')" 
                    :class="{ active: activeTab = 'Home' }" 
                    class="navbar-items"
                    v-if="this.activeTab !== 'Home' && this.darkMode === false">
                    <img src="../assets/home.png" />
                </div>
                <div 
                    @click="changeTab('Home')" 
                    :class="{ active: activeTab = 'Home' }" 
                    class="navbar-items"
                    v-if="this.activeTab === 'Home' && this.darkMode === true">
                    <img src="../assets/home_fill_dark.png" />
                </div>
                <div 
                    @click="changeTab('Home')" 
                    :class="{ active: activeTab = 'Home' }" 
                    class="navbar-items"
                    v-if="this.activeTab !== 'Home' && this.darkMode === true">
                    <img src="../assets/home_dark.png" />
                </div>
                <div @click="changeTab('Add-Member')" :class="{ active: activeTab === 'Accueil', dark: darkMode }" class="navbar-items">Home</div>
                <div @click="changeTab('Tree')" :class="{ active: activeTab === 'Tree', dark: darkMode }" class="navbar-items">Tree</div>
                <div @click="changeTab('Members')" :class="{ active: activeTab === 'Members', dark: darkMode }" class="navbar-items">Members</div>
            </div>

            <!-- NAVBAR / RIGHT ITEMS -->
            <div class="navbar-right">
                <div @click="toggleDarkMode" class="navbar-items sun">
                    <img v-if="darkMode" src="../assets/moon.png" title="DARK MODE" />
                    <img v-else src="../assets/sun.png" title="LIGHT MODE" />
                </div>
                <div 
                    @click="changeTab('Sign-Up')" 
                    :class="{ active: activeTab === 'SignUp' }" 
                    class="navbar-items" 
                    v-if="(this.activeTab === 'SignUp' || this.activeTab === 'SignIn') && this.darkMode === false"
                >
                    <img src="../assets/account_fill.png" />
                </div>
                <div 
                    @click="changeTab('Sign-Up')" 
                    :class="{ active: activeTab === 'SignUp' }" 
                    class="navbar-items"
                    v-if="(this.activeTab !== 'SignUp' && this.activeTab !== 'SignIn') && this.darkMode === false"
                >
                    <img src="../assets/account.png" />
                </div>
                <div 
                    @click="changeTab('Sign-Up')" 
                    :class="{ active: activeTab === 'SignUp' }" 
                    class="navbar-items"
                    v-if="(this.activeTab === 'SignUp' || this.activeTab === 'SignIn') && this.darkMode === true"
                >
                    <img src="../assets/account_fill_dark.png" />
                </div>
                <div 
                    @click="changeTab('Sign-Up')" 
                    :class="{ active: activeTab === 'SignUp' }" 
                    class="navbar-items"
                    v-if="(this.activeTab !== 'SignUp' && this.activeTab !== 'SignIn') && this.darkMode === true"
                >
                    <img src="../assets/account_dark.png" />
                </div>
            </div>
        </div>

    </div>
</template>

<script>
export default {
    computed: {
        darkMode() {
            return this.$store.state.darkMode;
        },
        activeTab() {
            const path = this.$route.path;
            if (path.startsWith('/home')) return 'Home';
            if (path.startsWith('/members')) return 'Members';
            if (path.startsWith('/tree')) return 'Tree';
            if (path.startsWith('/add-member')) return 'Accueil';
            if (path.startsWith('/sign-up')) return 'SignUp';
            if (path.startsWith('/sign-in')) return 'SignIn';
            return 'Home'; // Fallback
        }
    },
    methods: {
        changeTab(tab) {
            this.$router.push('/' + tab.toLowerCase());
        },
        toggleDarkMode() {
            this.$store.commit('toggleDarkMode');
        }
    }
}
</script>

<style>
.navbar {
    background-color: #fff; 
    display: flex; 
    flex-direction: horizontal;
    border-bottom:solid 1px var(--lightGrey);
    width: 100vw;
    position:fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    margin: 0 !important;
    padding: 4px;
    justify-content: space-between;
}

.navbar.dark {
    background-color:black;
}

.navbar-items {
    display: flex; 
    align-items: center;
    margin-right: 10px;
    color: black;
    cursor: pointer;
    padding:5px 15px;
    width:fit-content;
    border-radius: 10px;
}

.navbar-items.dark {
    color:white;
}

.navbar-items:hover {
    background-color: var(--lightGrey);
}

.navbar-items.active {
    font-weight: bold;
    color:black;
}

.navbar-items.dark.active {
    color:white;
}

.navbar-items:last-child {
    margin-left: auto;
    margin-right: 20px;
}

.navbar-items:nth-last-child(2) {
    margin-left: auto;
    margin-right:10px;
}

.navbar-left,
.navbar-right {
  display: flex;
  align-items: center;
}

</style>